import {
    Component,
    h,
    Prop,
    Event,
    EventEmitter,
    Host,
    State,
    Element,
} from '@stencil/core';
import { ColDef } from 'ag-grid-community';
import { fetchTranslations } from '../../../utils/translation';

/**
 * @category Data Tables
 * @description A component for filtering table data. Provides a user-friendly interface for filtering data from tables.
 * @internal
 */

export interface FilterEvent {
    id: string;
    value: string;
}

@Component({
    tag: 'at-table-filters',
    shadow: false,
})
export class AtTableFilters {
    /**
     * Column definitions used in your at-table
     */
    @Prop()
    col_defs: ColDef[];

    /**
     * Currently selected columns and filter values
     */
    @Prop({ mutable: true })
    selected: { id: string; value: string }[] = [];

    @State() translations: any;
    @Element() el: HTMLAtTableFiltersElement;

    /**
     * Emits id of column and filter value on change.
     */
    @Event() atChange: EventEmitter<FilterEvent[]>;

    async componentWillLoad() {
        this.translations = await fetchTranslations(this.el);
    }

    filterChangeHandler(event: CustomEvent, columnId: string) {
        event.stopPropagation();
        this.selected = this.selected.map((column) => {
            if (column.id === columnId)
                return { id: column.id, value: event.detail };
            return column;
        });
        this.atChange.emit(this.selected);
    }

    clearFilters = () => {
        this.selected = [];
        this.atChange.emit([]);
    };

    clearSingleFilter = (columnId: string) => {
        this.selected = this.selected.filter(
            (column) => column.id !== columnId,
        );
        this.atChange.emit(this.selected.length ? this.selected : []);
    };

    render() {
        return (
            this.col_defs && (
                <Host class="flex items-start gap-8">
                    <div class="bg-surface-0 flex min-h-[36px] flex-wrap items-end gap-8 rounded-md p-8">
                        {!this.selected.length && (
                            <p class="text-light">0 Filters</p>
                        )}
                        {this.selected.map((column) => (
                            <div class="flex items-center gap-2">
                                <at-input
                                    class="w-input-sm"
                                    label={column.id}
                                    prefix={column.id + ': '}
                                    value={column.value}
                                    onAtuiChange={(event: CustomEvent) =>
                                        this.filterChangeHandler(
                                            event,
                                            column.id,
                                        )
                                    }
                                >
                                    <div slot="input-actions">
                                        <at-button
                                            icon="cancel_outline"
                                            type="secondaryText"
                                            size="sm"
                                            onClick={() =>
                                                this.clearSingleFilter(
                                                    column.id,
                                                )
                                            }
                                            data-name={`filter-clear-${column.id}`}
                                        ></at-button>
                                    </div>
                                </at-input>
                            </div>
                        ))}

                        {this.selected.length > 0 && (
                            <at-button
                                type="secondaryText"
                                label="Clear All"
                                onClick={this.clearFilters}
                                data-name="filter-clear-all"
                            />
                        )}
                    </div>
                </Host>
            )
        );
    }
}
